<template lang="html">
  <v-ons-list-item expandable :expanded.sync="isExpanded" @click="closeAll();">
    {{ cmp.properties.title }}
    <div class="expandable-content">
        <v-ons-card>
          <i>
            {{ cmp.properties.content}}
          </i>
        </v-ons-card>
    </div>
  </v-ons-list-item>
</template>

<script>
import EventBus from "../Bus.js";

export default {
  props: ["cmp"],
  data() {
    return {
      isExpanded: true
    };
  },
  methods: {
    closeAll() {
      EventBus.$emit("ListItemClicked", this);
    }
  },
  mounted() {
    EventBus.$on("ListItemClicked", VueComponent => {
      if (this !== cmp) {
        cmp.isExpanded = false;
      }
    });
  }
};
</script>

